.advantages {
  // padding-top: 42px;
  padding-top: 116px;
  padding-bottom: 36px;

  font-size: 1.2rem;
  font-weight: 700;
  line-height: 1.7;
  letter-spacing: 0;

  color: var(--secondary-text-color-teme-dark);
}

@media screen and (min-width:480px) {
  .advantages {
    padding-top: 126px;
  }
}

@media screen and (min-width:768px) {
  .advantages {
    // padding-top: 48px;
    padding-top: 142px;
    padding-bottom: 40px;
  }
}

@media screen and (min-width:1200px) {
  .advantages {
    // padding-top: 60px;
    padding-top: 150px;
    padding-bottom: 84px;
  }
}

.advantages__list {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(3, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 94px;
}

@media screen and (min-width:480px) {
  .advantages__list {
    grid-row-gap: 98px;
  }
}

@media screen and (min-width:768px) {
  .advantages__list {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 80px;
    grid-row-gap: 0px;
  }
}

@media screen and (min-width:1200px) {
  .advantages__list {
    grid-column-gap: 32px;
  }
}

.advantages__item {
  position: relative;
  display: grid;
  justify-items: center;

  flex-shrink: 0;
}

.advantages__item::after {
  position: absolute;
  content: "";
  width: 50px;
  height: 50px;
  display: flex;

  background-color: var(--third-text-color-teme-dark);

  // background-image: url(mob-ice-cream-1x-min.f38d1b51.png);
  // background-position: 50%;
  // background-repeat: no-repeat;
  // background-size: cover;
  border-radius: 50%;

  top: -66px;
  //top=heigh+margin-bottom
}

@media screen and (min-width:768px) {
  .advantages__item::after {
    top: -82px;
  }
}

@media screen and (min-width:1200px) {
  .advantages__item::after {
    top: -90px;
  }
}

.advantages__item--volume::after {
  background-image: url('../images/advantages/cow-@1x.png');
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 36px 36px;
}

@media screen and (min-device-pixel-ratio: 2),
screen and (min-resolution: 192dpi),
screen and (min-resolution: 2dppx) {
  .advantages__item--volume::after {
    background-image: url('../images/advantages/cow-@2x.png');
  }
}

.advantages__item--weight::after {
  background-image: url('../images/advantages/weight-@1x.png');
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 36px 36px;
}

@media screen and (min-device-pixel-ratio: 2),
screen and (min-resolution: 192dpi),
screen and (min-resolution: 2dppx) {
  .advantages__item--weight::after {
    background-image: url('../images/advantages/weight-@2x.png');
  }
}

.advantages__item--sort::after {
  background-image: url('../images/advantages/ice-crema-@1x.png');
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 36px 36px;
}

@media screen and (min-device-pixel-ratio: 2),
screen and (min-resolution: 192dpi),
screen and (min-resolution: 2dppx) {
  .advantages__item--sort::after {
    background-image: url('../images/advantages/ice-crema-@2x.png');
  }
}

.advantages__subtitle {
  margin-bottom: 18px;
  font-size: 36px;
}

@media screen and (min-width:768px) {
  .advantages__subtitle {
    margin-bottom: 16px;
    font-size: 4.8rem;
  }
}

@media screen and (min-width:1200px) {
  .advantages__subtitle {
    margin-bottom: 8px;
    font-size: 5.8rem;
  }
}

.advantages__text {
  width: 230px;

  font-size: 1.2rem;
  line-height: 1.7;
  text-align: center;
}


@media screen and (min-width:768px) {
  .advantages__text {
    width: 100%;
    font-size: 1.4rem;
    font-weight: normal;
  }
}

@media screen and (min-width:1200px) {
  .advantages__text {
    width: 342px;

    margin: 0 auto;
    font-size: 1.6rem;
  }
}